.add_auto {
    &-header {
        width: 185px;
        height: 14px;
        color: #1c2931;
        font-family: Arial;
        font-size: 12px;
        font-weight: 700;
        margin-bottom: 60px;
        margin-top: 18px; } }
.nav-link {
    &__header {
        font-family: "Open Sans";
        font-size: 14px;
        font-weight: 700; }
    &__text {
        font-family: Arial;
        font-size: 12px;
        font-weight: 400;
        margin-top: 5px; }
    &__title {
        width: 520px;
        height: 26px;
        color: #2e393f;
        font-family: "Open Sans";
        font-size: 16px;
        font-weight: 700;
        margin-bottom: 10px; }
    &__subtitle {
        color: #1c2931;
        font-family: Arial;
        font-size: 14px;
        font-weight: 400; }
    &__question {
        color: #1c2931;
        font-family: Arial;
        font-size: 14px;
        font-weight: 700;
        margin-top: 34px; }
    &__items {
        display: flex;
        height: 210px;
        justify-content: space-between; }
    &__kuzov {
        color: #3e4950;
        font-family: Arial;
        font-size: 14px;
        font-weight: 400;
        margin-left: 18px; } }
.form-control {
    padding-left: 20px;
    width: 300px;
    margin-top: 15px;
    &-feedback {
        position: absolute;
        z-index: 2;
        display: block;
        width: 2.375rem;
        height: 2.375rem;
        line-height: 2.375rem;
        text-align: center;
        pointer-events: none;
        color: #aaa; } }
.image_kuzov {
    cursor: pointer;
    user-select: none;
    border: none;
    &:active {
        filter: invert(1); } }
