.weekDays-selector {
    width: 350px;
    margin: auto; }

.weekDays-selector input {
	display: none;
	&[type=checkbox] + label {
		display: inline-block;
		border-radius: 6px;
		background: #e6e6e6;
		height: 43px;
		width: 46px;
		line-height: 40px;
		text-align: center;
		cursor: pointer; }

	&[type=checkbox]:checked + label {
		background: blue;
		color: white; } }
.period {
    display: flex;
    width: 350px;
    margin: auto;
    justify-content: space-between;
    margin-top: 10px;
    &-text {
        margin-left: 23px;
        color: #2e393f;
        font-family: Arial;
        font-size: 13px;
        font-weight: 400;
        line-height: 32px; }
    &-countries {
        position: relative;
        &::before {
            content: '';
            display: block;
            position: absolute;
            cursor: pointer;
            width: 8px;
            height: 2px;
            background-color: #8ec740;
            -webkit-transform: rotate(-27deg);
            transform: rotate(-45deg);
            top: 20px;
            right: 15px;
            z-index: 1;
            pointer-events: none; }
        &::after {
            content: '';
            display: block;
            position: absolute;
            cursor: pointer;
            width: 8px;
            height: 2px;
            background-color: #8ec740;
            -webkit-transform: rotate(-140deg);
            transform: rotate(-145deg);
            top: 20px;
            right: 21px;
            z-index: 1;
            pointer-events: none; } }
    &-continents {
        position: relative;
        &::before {
            content: '';
            display: block;
            position: absolute;
            cursor: pointer;
            width: 8px;
            height: 2px;
            background-color: #8ec740;
            -webkit-transform: rotate(-27deg);
            transform: rotate(-45deg);
            top: 20px;
            right: 20px;
            z-index: 1;
            pointer-events: none; }
        &::after {
            content: '';
            display: block;
            position: absolute;
            cursor: pointer;
            width: 8px;
            height: 2px;
            background-color: #8ec740;
            -webkit-transform: rotate(-140deg);
            transform: rotate(-145deg);
            top: 20px;
            right: 26px;
            z-index: 1;
            pointer-events: none; } } }
#continents {
    width: 89px;
    height: 39px;
    padding-left: 15px;
    color: #2e393f;
    font-family: Arial;
    font-size: 13px;
    font-weight: 400;
    line-height: 32px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none; }
#selectcountries {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    padding-left: 15px;
    width: 94px;
    height: 39px;
    color: #2e393f;
    font-family: Arial;
    font-size: 13px;
    font-weight: 400;
    line-height: 32px; }
